.header-footer:not(.main-footer) {
  border-bottom: 1px solid var(--light-transparent-color);
}
.header-footer:not(.main-footer) > *:first-child {
  grid-column: 1/3;
}
.header-footer:not(.main-footer) > *:nth-child(2) {
  grid-column: 3/6;
}
.header-footer:not(.main-footer) > *:nth-child(3) {
  grid-column: 6/9;
}
.header-footer:not(.main-footer) > *:last-child {
  grid-column: 9/12;
}
@media (max-width: 1300px) {
  .header-footer:not(.main-footer) {
    display: flex !important;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    gap: 2rem !important;
  }
}
@media (max-width: 1300px) and (max-width: 992px) {
  .header-footer:not(.main-footer) {
    gap: 0 !important;
  }
}
@media (max-width: 1300px) {
  .header-footer:not(.main-footer) > * {
    flex: 0 0 auto;
  }
}
@media (max-width: 1300px) and (max-width: 992px) {
  .header-footer:not(.main-footer) > * {
    flex: 0 0 100%;
    justify-content: center;
  }
}
@media (max-width: 1300px) {
  .header-footer:not(.main-footer) > *:first-child {
    flex: 0 0 100%;
    justify-content: center;
  }
  .header-footer:not(.main-footer) .btn {
    display: inline-block;
    margin: 0 auto;
    flex: 0 0 50%;
  }
}
@media (max-width: 1300px) and (max-width: 992px) {
  .header-footer:not(.main-footer) .btn {
    flex: 0 0 100%;
  }
}
.header-footer:not(.main-footer) > *:not(.btn) {
  font-family: var(--font-unbounded);
}

.header-footer.main-footer {
  grid-template-columns: repeat(10, 1fr);
}
.header-footer.main-footer > *:first-child {
  grid-column: 1/4;
}
@media (max-width: 1200px) {
  .header-footer.main-footer > *:first-child {
    grid-column: 1/5;
  }
}
.header-footer.main-footer > *:nth-child(2) {
  grid-column: 4/11;
}
@media (max-width: 1200px) {
  .header-footer.main-footer > *:nth-child(2) {
    grid-column: 5/11;
  }
}
.header-footer.main-footer > *:last-child {
  grid-column: 1/11;
}
@media (max-width: 992px) {
  .header-footer.main-footer > * {
    grid-column: 1/11 !important;
  }
}

.footer-tel__icon {
  width: 26px;
  height: 24px;
}

.footer-email__icon {
  width: 30px;
  height: 23px;
}

@media (max-width: 768px) {
  .footer .logo__title {
    font-size: 1.3125rem !important;
  }
}
.footer::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0.4;
}

.footer-tg {
  width: 33px;
  height: 29px;
  color: var(--bs-white);
}

.footer-vk {
  width: 30px;
  height: 30px;
  color: var(--bs-white);
}

.footer-github {
  width: 30px;
  height: 30px;
}

.header-footer.contacts {
  grid-template-columns: repeat(11, 1fr);
}
@media (max-width: 1200px) {
  .header-footer.contacts {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.menu-footer__title {
  font-weight: 500;
  font-size: 1.125rem;
}
.menu-footer__link {
  font-size: 1rem;
}
.menu-footer__link a {
  color: rgba(255, 255, 255, 0.8);
}

.main-footer a:hover {
  text-decoration: underline;
}
@media (max-width: 768px) {
  .main-footer__item:first-child {
    font-size: 1rem;
  }
}
.main-footer__description a {
  color: var(--bs-pink);
  font-weight: 500;
}
.main-footer__wrapper {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
@media (max-width: 1200px) {
  .main-footer__wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  .main-footer__wrapper {
    grid-template-columns: 1fr;
  }
}
.main-footer__wrapper:not(:last-child):before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 75%;
  height: 1px;
  background-color: var(--light-transparent-color);
}
.main-footer__text {
  font-size: 1.125rem;
}

.overflow-x-hidden {
  overflow-x: hidden;
}

.background-footer-blur {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 250vw;
  -webkit-transform: translate(-50%, 0);
  -ms-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  height: 5000px;
  z-index: -1;
}

.background-footer-blur:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(50% 50% at bottom, #29278a 0%, #1d1a66 39.5%, #110d30 70.99%, #06040d 100%);
}

.background-footer-blur svg {
  position: absolute;
  bottom: -36%;
  left: 50%;
  width: 100vw;
  height: 100%;
  -webkit-transform: translate(-50%, 0);
  -ms-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}